import React from 'react'
import Link from 'next/link'

const Header: React.FC = () => {
  return (
    <header style={{
      background: 'rgba(255, 255, 255, 0.95)',
      backdropFilter: 'blur(10px)',
      borderBottom: '1px solid var(--border)',
      padding: '1rem 2rem',
      position: 'sticky',
      top: 0,
      zIndex: 1000,
      boxShadow: 'var(--shadow)'
    }}>
      <nav style={{
        maxWidth: '1200px',
        margin: '0 auto',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
      }}>
        <Link href="/" style={{ textDecoration: 'none' }}>
          <img src="/assets/logo.svg" alt="Essential Business Solutions Services Logo" style={{ height: '50px', width: 'auto' }} />
        </Link>
        <ul style={{
          display: 'flex',
          listStyle: 'none',
          gap: '2rem',
          margin: 0,
          padding: 0
        }}>
          <li><Link href="/" style={{ color: 'var(--text-primary)', fontWeight: '500' }}>Home</Link></li>
          <li><Link href="/services" style={{ color: 'var(--text-primary)', fontWeight: '500' }}>Services</Link></li>
          <li><Link href="/about" style={{ color: 'var(--text-primary)', fontWeight: '500' }}>About</Link></li>
          <li><Link href="/contact" style={{ color: 'var(--text-primary)', fontWeight: '500' }}>Contact</Link></li>
          <li><Link href="/booking" className="btn" style={{ margin: 0 }}>Book Now</Link></li>
        </ul>
      </nav>
    </header>
  )
}

export default Header